export default {
name: "",
components: {

},
props: {},
data () {
  return {
	  username:'',
	  password:'',
	  
    }
  },
  methods: {
     goBack(){
     	uni.navigateBack({
     		delta:2
     	})
     },
	 inputText(e){
		 this.username=e.detail.value
		
	 },
	 inputPassword(e){
		  this.password=e.detail.value
	 },
	 login(){
		 uni.request({
		 	url:`https://www.imovietrailer.com/superhero/user/registOrLogin?&qq=2684425594`,
				data: {
						username: this.username,
						password: this.password
					},
		 	method:'POST',
		 	success: (res) => {
				console.log(res);
				if(res.data.status===200){
					 this.$store.state.message=res.data.data
					 alert("登陆成功")
					 uni.navigateTo({
					 		url:'../message/message'
					 	})
					 }
					 else{
						 alert(res.data.msg)
				}
		 	  }
		 })
		 
	 }
	 
  },
  mounted () {

  },
  onLoad () {
  
  },
  filters: {

  },
  computed: {
 
  },
  watch: {

  },
  directives: {

  }
}
